import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Row, Col, Icon } from 'antd';

import MobileMenu from 'components/mobile-menu';

import './style.scss';

export function Header() {
  const [mobileMenuVisibility, setMobileMenuVisibility] = useState(false);

  function handleMobileMenuVisibility(isOpen = false) {
    setMobileMenuVisibility(isOpen);
  }

  return (
    <>
      {mobileMenuVisibility && (
        <MobileMenu handleClose={() => handleMobileMenuVisibility(false)} />
      )}
      <header className="pd-header">
        <Row type="flex" align="middle" gutter={16}>
          <Col xs={0} lg={5}>
            <div className="pd-header-logo">
              <Link to="/">Élisée</Link>
            </div>
          </Col>
          <Col xs={2} lg={0}>
            <Icon
              className="menu-icon"
              onClick={() => handleMobileMenuVisibility(true)}
              type="menu"
            />
          </Col>
          <Col xs={16} lg={5}></Col>
          <Col xs={6} lg={14}>
            <div className="pd-header-right">
              <Link to="/engagement-rings">Engagement Rings</Link>
              <Link to="/wedding-rings">Wedding Rings</Link>
              <Link to="/diamonds">Diamonds</Link>
              <Link to="/education">Education</Link>
            </div>
          </Col>
        </Row>
      </header>
    </>
  );
}

export default { Header };
