import React, { Fragment } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import { Layout } from 'antd';

import { StoreProvider } from 'store';

import AuthenticationContext from './contexts/authentication';

// CustomerFacing
import { Home } from 'pages/home';
import { About } from 'pages/about';
import { Careers } from 'pages/careers';
import { Contact } from 'pages/contact';

// ContentManagement
import { Home as DashboardHome } from 'pages/dashboard/home';

// CustomerFacing
import { Header } from 'components/header';
import { Footer } from 'components/footer';

// ContentManagement
import { LoginPageForm } from 'pages/login';
import { Sidebar } from 'components/sidebar';

import AuthenticatedGuardRoute from 'components/guard/authenticated';
import NotAuthenticatedGuardRoute from 'components/guard/notAuthenticated';

import 'antd/dist/antd.css';
import 'App.scss';

function CustomerFacingWrapper() {
  return (
    <Fragment>
      <Header />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about" component={About} />
        <Route exact path="/careers" component={Careers} />
        <Route exact path="/contact" component={Contact} />
      </Switch>
      <Footer />
    </Fragment>
  );
}

function DashboardWrapper() {
  return (
    <AuthenticationContext>
      <Switch>
        <NotAuthenticatedGuardRoute
          exact
          path="/dashboard/login"
          component={LoginPageForm}
        />
        <Layout className="pd-cms">
          <Sidebar />
          <Switch>
            <AuthenticatedGuardRoute
              path="/dashboard"
              component={DashboardHome}
            />
            <AuthenticatedGuardRoute
              exact
              path="/dashboard/home"
              component={DashboardHome}
            />
          </Switch>
        </Layout>
      </Switch>
    </AuthenticationContext>
  );
}

function App() {
  return (
    <Router>
      <StoreProvider>
        <Switch>
          <Route path="/dashboard" component={DashboardWrapper} />
          <Route path="/" component={CustomerFacingWrapper} />
        </Switch>
      </StoreProvider>
    </Router>
  );
}

export default App;
