import React from 'react';
import { Menu, Layout, Typography } from 'antd';
import { Link } from 'react-router-dom';

import AuthenticationApi from 'api/authentication';

import './style.scss';

const { Sider } = Layout;
const { SubMenu } = Menu;
const { Title } = Typography;

const menuOptions = [
  {
    type: 'SubMenu',
    name: 'Orders',
    children: [
      { type: 'Menu', name: 'Sub Menu 1', link: '/dashboard/orders/1' },
      { type: 'Menu', name: 'Sub Menu 2', link: '/dashboard/orders/2' },
      { type: 'Menu', name: 'Sub Menu 3', link: '/dashboard/orders/3' },
    ],
  },
  {
    type: 'SubMenu',
    name: 'Products',
    children: [
      { type: 'Menu', name: 'Sub Menu 1', link: '/dashboard/products/1' },
      { type: 'Menu', name: 'Sub Menu 2', link: '/dashboard/products/2' },
    ],
  },
];

function renderMenu(opt, parent = '') {
  const { name = '', link = '', type = 'Menu', children = [] } = opt;

  if (type === 'Menu') {
    return (
      <Menu.Item key={`${parent}${name}`}>
        <Link to={link}>{name}</Link>
      </Menu.Item>
    );
  } else {
    return (
      <SubMenu key={name} title={<span>{name}</span>}>
        {children.map((opt) => renderMenu(opt, `${name}-`))}
      </SubMenu>
    );
  }
}

export function Sidebar() {
  async function handleLogout() {
    const cms_token = localStorage.getItem('cms_token');

    if (cms_token) {
      AuthenticationApi.logout(cms_token);
      localStorage.removeItem('cms_token');
      localStorage.removeItem('current_admin');
    }
  }

  return (
    <Sider width="300" className="pd-cms-sidebar">
      <div className="wrapper">
        <div className="menu">
          <Title level={3}>
            <Link to={'/dashboard'}>Dashboard</Link>
          </Title>
          <Menu mode="inline">{menuOptions.map((opt) => renderMenu(opt))}</Menu>
        </div>
        <div className="user-badge">
          <Title level={4} onClick={() => handleLogout()}>
            <Link to={'/dashboard/login'}>Logout</Link>
          </Title>
        </div>
      </div>
    </Sider>
  );
}

export default { Sidebar };
